import * as React from "react";
import Container from "@mui/material/Container";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Divider from "@mui/material/Divider";
import ToggleButton from "@mui/material/ToggleButton";
import ToggleButtonGroup from "@mui/material/ToggleButtonGroup";
import Grid from '@mui/material/Grid2';
import TextField from "@mui/material/TextField";



// Components 
import Todo from "./Todo";

// DIALOG IMPORTS
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";

// OTHERS
import { useTodos } from "../contexts/todosContext";
import { useToast } from "../contexts/ToastContext";
import { useContext, useState, useEffect, useMemo } from "react";

export default function TodoList() {
	// array of tasks
	const {todos , dispatch} = useTodos()
	const { showHideToast } = useToast();

	const [dialogTodo, setDialogTodo] = useState(null);
	const [showDeleteDialog, setShowDeleteDialog] = useState(false);
	const [showUpdateDialog, setShowUpdateDialog] = useState(false);
	// title Input
	const [titleInput, setTitleInput] = useState("");
	const [displayedTodosType, setDisplayedTodosType] = useState("all");

	// filteration arrays

	const completedTodos = useMemo(() => {
		return todos.filter((t) => {
			return t.isCompleted;
		});
	}, [todos]);

	const notCompletedTodos = useMemo(() => {
		return todos.filter((t) => {
			return !t.isCompleted;
		});
	}, [todos]);

	let todosToBeRendered = todos; 

	if (displayedTodosType == "completed") {
		todosToBeRendered = completedTodos;
	} else if (displayedTodosType == "non-completed") {
		todosToBeRendered = notCompletedTodos;
	} else {
		todosToBeRendered = todos;
	}

	useEffect(() => {
		dispatch({type:'loaded'})
	}, []);

	// ===== HANDLERS
	function changeDisplayedType(e) {
		setDisplayedTodosType(e.target.value);
	}

	function handleAddClick() {
		dispatch({type:'added',payload:{title:titleInput}})
		setTitleInput("");
		showHideToast("Added successful");
	}

	function openDeleteDialog(todo) {
		setDialogTodo(todo);
		setShowDeleteDialog(true);
	}

	function openUpdateDialog(todo) {
		setDialogTodo(todo);
		setShowUpdateDialog(true);
	}

	function handleDeleteDialogClose() {
		setShowDeleteDialog(false);
	}

	function handleDeleteConfirm() {
		dispatch({type:'deleted',payload:{task:dialogTodo}})
		setShowDeleteDialog(false);
		showHideToast("Deleted successful");
	}

	function handleUpdateClose() {
		setShowUpdateDialog(false);
	}

	function handleUpdateConfirm() {
		dispatch({type:'updated' , payload:{task:dialogTodo}})
		setShowUpdateDialog(false);
		showHideToast("Edited successful");
	}

	const todosJsx = todosToBeRendered.map((t) => {
		return (
			<Todo
				key={t.id}
				todo={t}
				showDelete={openDeleteDialog}
				showUpdate={openUpdateDialog}
			/>
		);
	});

	return (
		<> 
			{/* DELETE DIALOG */}
			<Dialog
				onClose={handleDeleteDialogClose}
				open={showDeleteDialog}
				aria-labelledby="alert-dialog-title"
				aria-describedby="alert-dialog-description"
			>
				<DialogTitle id="alert-dialog-title">
					Are you sure you want to delete this task?
				</DialogTitle> 
				<DialogContent>
					<DialogContentText id="alert-dialog-description">
						You cannot undo after deletion!
					</DialogContentText>
				</DialogContent>
				<DialogActions>
					<Button onClick={handleDeleteDialogClose}>Close</Button>
					<Button autoFocus onClick={handleDeleteConfirm}>
						Delete
					</Button>
				</DialogActions>
			</Dialog>
			{/* === DELETE DIALOG === */}

			{/* UPDATE DIALOG */}
			<Dialog
				onClose={handleUpdateClose}
				open={showUpdateDialog}
				aria-labelledby="alert-dialog-title"
				aria-describedby="alert-dialog-description"
			>
				<DialogTitle id="alert-dialog-title">Edit task</DialogTitle>
				<DialogContent>
					<TextField
						autoFocus
						margin="dense"
						id="name"
						label="taskTitle"
						fullWidth
						variant="standard"
						value={dialogTodo?.title}
						onChange={(e) => {
							setDialogTodo({
								...dialogTodo,
								title: e.target.value,
							});
						}}
					/>

					<TextField
						autoFocus
						margin="dense"
						id="name"
						label="Details"
						fullWidth
						variant="standard"
						value={dialogTodo?.details}
						onChange={(e) => {
							setDialogTodo({
								...dialogTodo,
								details: e.target.value,
							});
						}}
					/>
				</DialogContent>
				<DialogActions>
					<Button onClick={handleUpdateClose}>close</Button>
					<Button autoFocus onClick={handleUpdateConfirm}>
						Agree
					</Button>
				</DialogActions>
			</Dialog>
			{/* === UPDATE DIALOG */}

			<Container maxWidth="sm ">
				<Card
					sx={{ minWidth: 275 }}
					style={{
						maxHeight: "80vh",
						overflowY: "scroll",
                        scrollbarWidth:'none',
					}}
				> 
					<CardContent>
						<Typography variant="h2" style={{ fontWeight: "bold" }}>
							My Tasks
						</Typography>
						<Divider />

						{/* FILTER BUTTONS */}
						<ToggleButtonGroup
							style={{ marginTop: "30px" }}
							value={displayedTodosType}
							exclusive
							onChange={changeDisplayedType}
							aria-label="text alignment"
							color="primary"
						>
							<ToggleButton value="all">
								 All
							</ToggleButton>
							<ToggleButton value="completed">
								Done
							</ToggleButton>
							<ToggleButton value="non-completed">Go</ToggleButton>
						</ToggleButtonGroup>
						{/* ==== FILTER BUTTON ==== */}

						{/* ALL TODOS */}
						{todosJsx}
						{/* === ALL TODOS === */}

						{/* INPUT + ADD BUTTON */}
						<Grid
							container
							style={{ marginTop: "20px" }}
							display='flex'
							justifyContent='space-between'
							spacing={2}
						>
							<Grid
								sx={{width:'70%'}}
								display="flex"
								justifyContent="space-around"
								alignItems="center"
							>
								<TextField
									style={{ width: "100%" }}
									id="outlined-basic"
									label="Enter your task"
									variant="outlined"
									value={titleInput}
									onChange={(e) => {
										setTitleInput(e.target.value);
									}}
								/>
							</Grid>

							<Grid
								sx={{width:'25%'}}
								display="flex"
								justifyContent="space-between"
								alignItems="center"
							>
								<Button
									style={{ width: "100%", height: "100%" }}
									variant="contained"
									onClick={() => {
										handleAddClick();
									}}
									disabled={titleInput.length == 0}
								>
									Adding
								</Button>
							</Grid>
						</Grid>
						{/*== INPUT + ADD BUTTON ==*/}
					</CardContent>
				</Card>
			</Container>
		</>
	);
}
